/*********************
REUSABLE ITEMS
*********************/
.clearfix, .cf {
  zoom: 1; }
  .clearfix::before, .clearfix::after, .cf::before, .cf::after {
    content: "";
    display: table; }
  .clearfix::after, .cf::after {
    clear: both; }

.modern-clearfix {
  display: flow-root; }

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important; }
  .screen-reader-text:focus {
    background-color: #fff;
    clip: auto !important;
    clip-path: none;
    color: #547E2A;
    display: block;
    font-size: 1em;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

.nowrap {
  display: inline-block; }

