/******************************************************************
H1, H2, H3, H4, H5 STYLES
******************************************************************/
h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  clear: both;
  margin-top: 16px;
  margin-bottom: 16px; }
  h1 a, h1 a:visited, .h1 a, .h1 a:visited, h2 a, h2 a:visited, .h2 a, .h2 a:visited, h3 a, h3 a:visited, .h3 a, .h3 a:visited, h4 a, h4 a:visited, .h4 a, .h4 a:visited, h5 a, h5 a:visited, .h5 a, .h5 a:visited, h6 a, h6 a:visited, .h6 a, .h6 a:visited {
    text-decoration: none; }

h1, .h1 {
  font-size: 2em;
  line-height: 1.5em; }

h2, .h2 {
  font-size: 1.5em;
  line-height: 1.5em; }

h3, .h3 {
  font-size: 1.25em; }

h4, .h4 {
  font-size: 1.125em; }

h5, .h5 {
  font-size: 1em;
  text-transform: uppercase;
  letter-spacing: 2px; }

h6, .h6 {
  font-size: .875em;
  text-transform: uppercase;
  letter-spacing: 2px; }

@media screen and (min-width: 48em) {
  h1, .h1 {
    font-size: 2.5em; }
  h2, .h2 {
    font-size: 1.75em; }
  h3, .h3 {
    font-size: 1.37em; } }

/*********************
ENTRY CONTENT
*********************/
.entryContent p {
  margin: 0 0 1.5em; }

.entryContent ul, .entryContent ol {
  margin: 0 0 1.5em 2em; }

.entryContent ul {
  list-style: disc; }

.entryContent ol {
  list-style: decimal; }

.entryContent li > ul,
.entryContent li > ol {
  margin-bottom: 0;
  margin-left: 1em; }

.entryContent dt {
  font-weight: bold; }

.entryContent dd {
  margin: 0 1em 1.5em; }

.entryContent table {
  margin: 0 0 1.5em;
  width: 100%; }

.entryContent th {
  font-weight: bold; }

/*********************
BASE ELEMENTS
*********************/
.byline {
  font-size: .75em;
  margin: 1em 0; }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 0 1.5em 1em;
  padding: 0 0 0 1em;
  border-left: 3px solid #547E2A;
  font-style: italic;
  font-family: "Georgia", Cambria, Times, serif;
  color: #666; }

address {
  margin: 0 0 1.5em; }

pre {
  background: #111;
  color: #999;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 0.9em;
  margin: 0 0 1.5em;
  max-width: 100%;
  padding: 1em;
  overflow: auto; }

abbr, acronym {
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

/******************************************************************

Stylesheet: WP Media

******************************************************************/
.size-auto,
.size-full,
.size-large,
.size-medium,
.size-thumbnail {
  max-width: 100%;
  height: auto; }

@media screen and (min-width: 30em) {
  .alignleft, img.alignleft, figure.alignleft, dl.alignleft {
    display: inline;
    margin-left: 0em;
    margin-right: 1.5em; }
  .alignright, img.alignright, figure.alignright, dl.alignright {
    display: inline;
    margin-left: 1.5em;
    margin-right: 0em; }
  .aligncenter, img.aligncenter, figure.aligncenter, dl.aligncenter {
    clear: both;
    display: block;
    margin-right: auto;
    margin-left: auto; } }

.alignnone, img.alignnone, figure.alignnone, dl.alignnone, .alignnone img {
  margin-left: 0;
  margin-right: 0;
  height: auto; }

/*********************
LAZYLOADING IMAGES - uses lazysizes script
*********************/
IMG[src=''], IMG:not([src]) {
  opacity: 0; }

.lazyhd,
.lazysquare,
.lazy70 {
  position: relative;
  height: 0;
  background: #999; }
  .lazyhd img,
  .lazysquare img,
  .lazy70 img {
    width: 100%;
    height: auto;
    opacity: 0.2;
    transition: opacity 0.6s ease-in; }

.lazyhd {
  padding-bottom: 56.25%; }

.lazysquare {
  padding-bottom: 100%; }

.lazy70 {
  padding-bottom: 70%; }

.lazyload {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; }

.lazyloading {
  opacity: 0.2; }

.lazyloaded {
  opacity: 1 !important; }

/*********************
WP CAPTIONS
*********************/
.wp-caption {
  max-width: 100%;
  text-align: center;
  /* images inside wp-caption */ }
  .wp-caption.alignleft {
    margin-right: 1.5em; }
  .wp-caption.alignright {
    margin-left: 1.5em; }
  .wp-caption.aligncenter {
    margin-right: auto;
    margin-left: auto; }
  .wp-caption img {
    max-width: 100%;
    height: auto;
    margin-bottom: 0; }
  .wp-caption .wp-caption-text {
    font-size: 0.875em;
    margin: .5em 0 1em;
    text-align: center; }

/*********************
HTML5 IMAGE GALLERY
*********************/
.gallery {
  text-align: justify; }
  .gallery .gallery-item {
    margin: 0;
    width: 50%;
    display: inline-block;
    vertical-align: top;
    padding: .5em; }
  .gallery img {
    display: block; }
  .gallery .gallery-caption {
    text-align: left;
    padding: .25em 0; }
  .gallery::after {
    content: '';
    display: inline-block;
    width: 100%; }
  @media screen and (min-width: 48em) {
    .gallery .gallery-item {
      width: 25%; }
    .gallery img {
      will-change: opacity; }
      .gallery img:hover {
        opacity: .7;
        transition: opacity 0.2s ease-in-out; } }

/*********************
RESPONSIVE VIDEOS
*********************/
.videoContainer {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden; }

.videoContainer iframe,
.videoContainer object,
.videoContainer embed,
.videoContainer video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

/******************************************************************

Stylesheet: Buttons

******************************************************************/
button {
  background: none;
  border: none; }

.form-submit input,
.button, .button:visited {
  display: inline-block;
  position: relative;
  font-family: "Helvetica Neue", Arial, sans-serif;
  text-decoration: none;
  font-size: .875em;
  font-weight: bold;
  line-height: 1em;
  padding: 1em;
  border-radius: 2px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  -webkit-appearance: none; }

.form-submit input,
.button, .button:visited {
  background-color: #547E2A;
  border: 1px solid #547E2A;
  color: #fff; }
  .form-submit input:hover, .form-submit input:focus,
  .button:hover,
  .button:focus, .button:visited:hover, .button:visited:focus {
    background-color: #3b581d;
    color: #fff;
    text-decoration: none; }
  .form-submit input:active,
  .button:active, .button:visited:active {
    background-color: #213211;
    color: #fff; }

.pageHeader {
  text-align: center; }
  .pageHeader #logo {
    display: inline-block;
    padding: 2em; }

